"use client";

import { useEffect, useState } from "react";
import { db } from "@/lib/firebase";
import { collection, getDocs, query, where, Timestamp } from "firebase/firestore";

export interface Item {
  id?: string;
  name: string;
  slug: string;
  createdAt?: Timestamp;
}

interface Props {
  params: { slug: string };
}

export default function ItemDetails({ params }: Props) {
  const [item, setItem] = useState<Item | null>(null);

  useEffect(() => {
    if (!params.slug) return;

    const loadItem = async () => {
      const q = query(collection(db, "items"), where("slug", "==", params.slug));

      const snapshot = await getDocs(q);

      if (!snapshot.empty) {
        const data = snapshot.docs[0].data() as Item;
        setItem(data);
      }
    };

    loadItem();
  }, [params.slug]);

  if (!item) {
    return <div className="p-10">Loading...</div>;
  }

  return (
    <div className="max-w-3xl mx-auto mt-10">
      <h1 className="text-3xl font-semibold mb-4">{item.name}</h1>

      <p className="text-gray-600">Slug: {item.slug}</p>

      <p className="mt-4">
        Created: {item.createdAt instanceof Timestamp ? item.createdAt.toDate().toLocaleString() : "â€”"}
      </p>
    </div>
  );
}
